@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.recipe {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-right: auto;
    margin-left: auto;
    margin-top: 40px;
}
ul {
    list-style: none;
}
h1 {
    font-family: 'Oswald', sans-serif;
}
.recipe-post {
    background-color: rgba(235, 161, 173, 0.603);
    margin: 30px;
    padding: 10px;
    width: 350px;
    box-shadow: 0 10px 20px -2px rgb(235, 161, 173);
    border-radius: 20px;
}


.App {
  text-align: center;
  margin: 20px;
}
@media (max-width: 300px) {
  .search-bar {
    width: 50px;
  /* Fix this section of the searchbar */
  }
}
.search-bar {
  /* height: 30px;
  widows: 100%;
  border-radius: 10px; */
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;  
  border-color: pink;
  height: auto;
  width: 100px;
  box-shadow: 0 10px 20px -2px gray;
  padding: 12px 35px;
  margin-top: 20px;
}
.search-button {
  /* height: 30px;
  border-radius: 10px; */
  background-color: pink;
  width: auto;
  height: auto;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  box-shadow: 0 10px 20px -2px gray;
  padding: 12px 35px;
  font-weight: 700;
  position: relative;

}

.search-button:hover{
  background-color: rgb(235, 161, 173);
}
.search-form {
  min-height: 10hv;
}
.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.head-title {
  color: pink;
}
.head-paragraph {
  width: 400px;
  margin-left: auto;
  margin-right: auto;
}
.head {
  display: flex;
  flex-direction: column;
}

@media (max-width: 320px) {
    /* .search-button {
      border-radius: 30px;
    }
    .search-bar{
      border-radius: 30px;
    } */
    .App {
      margin-left: 20px;
      margin-right: auto;
    }
}
